import css from "./ContactForm.module.css"; //    
import { nanoid } from 'nanoid';
import {useState} from 'react'; //     

//  
import { useSelector, useDispatch } from "react-redux";
import { addContact } from "../../redux/operetions";
import { getContacts } from "../../redux/selectors";


export const ContactForm =()=> {

  const [name, setName] = useState('');
  const [number, setNumber] = useState('');


  const dispatch = useDispatch();//      
  const contacts = useSelector(getContacts);//   


    // INPUT -      
    const handleChange = (event) => {
      const {name, value} = event.currentTarget;
      if (name === "name") {setName(value);}
      if (name === "number") {setNumber(value);}
    };


    // ADD CONTACT -    
    const addContacts = ({ name, number }) => {
      if (contacts.some(value => value.name.toLocaleLowerCase() === name.toLocaleLowerCase())) {
            alert(`${name} is alredy in contacts`); //  ,   
      } else {
            const contact = {
              id: nanoid(),
              name: name,
              phone: number,
            };
            dispatch(addContact( contact )); // ADD CONTACT - 
      }
    }

    //   
      return (
        <>
        <form className={css.form} 
                  onSubmit={evt => {
                    evt.preventDefault(); //   
                    addContacts({ name, number }); //     addContact  (props)   .
                    setName("") //   
                    setNumber(""); //   
                  }}>
        <label htmlFor="name">Name</label>
        <input
          value={name}
          onChange={handleChange}
          className={css.form__input}
          type="text"
          name="name"
          pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
          title="Name may contain only letters, apostrophe, dash and spaces. For example Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan"
          required/>
        <label htmlFor="number">Number</label>
        <input
          value={number}
          onChange={handleChange}
          className={css.form__input}
          type="tel"
          name="number"
          pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
          title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
          required
        />

        <button className={css.form__btn} type="submit">Add contact</button>
        </form>
        </>
        
    ); }
